import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { catchError, map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class ApiService {
  private baseUrl = 'https://tmpro-5265168c0988.herokuapp.com/api';

  constructor(private http: HttpClient) {}

  // ** Equipos **
  getTeams(): Observable<any[]> {
    return this.http.get<any[]>(`${this.baseUrl}/teams`).pipe(
      map(response => Array.isArray(response) ? response : []),
      catchError(error => {
        console.error('Error al obtener equipos:', error);
        return of([]); // Retorna un array vacío en caso de error
      })
    );
  }

  createTeam(team: any): Observable<any> {
    return this.http.post<any>(`${this.baseUrl}/teams`, team);
  }

  updateTeam(id: number, team: any): Observable<any> {
    return this.http.put<any>(`${this.baseUrl}/teams/${id}`, team);
  }

  deleteTeam(id: number): Observable<any> {
    return this.http.delete<any>(`${this.baseUrl}/teams/${id}`);
  }

  // ** Jugadores **
  getPlayers(): Observable<any[]> {
    return this.http.get<any[]>(`${this.baseUrl}/players`).pipe(
      map(response => Array.isArray(response) ? response : []),
      catchError(error => {
        console.error('Error al obtener jugadores:', error);
        return of([]); // Retorna un array vacío en caso de error
      })
    );
  }

  createPlayer(player: any): Observable<any> {
    return this.http.post<any>(`${this.baseUrl}/players`, player);
  }

  updatePlayer(id: number, player: any): Observable<any> {
    return this.http.put<any>(`${this.baseUrl}/players/${id}`, player);
  }

  deletePlayer(id: number): Observable<any> {
    return this.http.delete<any>(`${this.baseUrl}/players/${id}`);
  }
}
