import { Component, OnInit } from '@angular/core';
import { ApiService } from '../services/api.service';

@Component({
  selector: 'app-team',
  templateUrl: './team.component.html',
  styleUrls: ['./team.component.css']
})
export class TeamComponent implements OnInit {
  teams: any[] = [];
  newTeam: any = {};
  selectedTeam: any = null;

  constructor(private apiService: ApiService) { }

  ngOnInit(): void {
    this.loadTeams();
  }

  loadTeams(): void {
    this.apiService.getTeams().subscribe(teams => {
      this.teams = teams;
    });
  }

  onSubmit(): void {
    if (this.selectedTeam && this.selectedTeam.id !== undefined) {
      this.apiService.updateTeam(this.selectedTeam.id, this.newTeam).subscribe(() => {
        this.loadTeams();
        this.clearForm();
      });
    } else {
      this.apiService.createTeam(this.newTeam).subscribe(() => {
        this.loadTeams();
        this.clearForm();
      });
    }
  }

  editTeam(team: any): void {
    if (team.id !== undefined) { // Verificación de que el id existe
      this.selectedTeam = team;
      this.newTeam = { ...team };
    }
  }

  deleteTeam(id: number | undefined): void {
    if (id !== undefined) { // Verificación de que el id existe
      this.apiService.deleteTeam(id).subscribe(() => {
        this.loadTeams();
      });
    }
  }

  clearForm(): void {
    this.newTeam = {};
    this.selectedTeam = null;
  }
}
