import { Component, OnInit } from '@angular/core';
import { ApiService } from '../services/api.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  teams: any[] = [];
  players: any[] = [];

  constructor(private apiService: ApiService, private router: Router) { }

  ngOnInit(): void {
    this.loadTeams();
    this.loadPlayers();
  }

  loadTeams(): void {
    this.apiService.getTeams().subscribe(teams => {
      this.teams = teams;
    });
  }

  loadPlayers(): void {
    this.apiService.getPlayers().subscribe(players => {
      this.players = players;
    });
  }

  goToEditTeam(team: any): void {
    this.router.navigate(['/team', team.id]);
  }

  goToCreateTeam(): void {
    this.router.navigate(['/team']);
  }

  goToEditPlayer(player: any): void {
    this.router.navigate(['/player', player.id]);
  }

  goToCreatePlayer(): void {
    this.router.navigate(['/player']);
  }

  deleteTeam(id: number): void {
    this.apiService.deleteTeam(id).subscribe(() => {
      this.loadTeams();
    });
  }

  deletePlayer(id: number): void {
    this.apiService.deletePlayer(id).subscribe(() => {
      this.loadPlayers();
    });
  }
}
