import { Component, OnInit } from '@angular/core';
import { ApiService } from '../services/api.service';

@Component({
  selector: 'app-player',
  templateUrl: './player.component.html',
  styleUrls: ['./player.component.css']
})
export class PlayerComponent implements OnInit {
  players: any[] = [];
  teams: any[] = [];
  newPlayer: any = {};
  selectedPlayer: any = null;

  constructor(private apiService: ApiService) { }

  ngOnInit(): void {
    this.loadPlayers();
    this.loadTeams();
  }

  loadPlayers(): void {
    this.apiService.getPlayers().subscribe(players => {
      this.players = players;
    });
  }

  loadTeams(): void {
    this.apiService.getTeams().subscribe(teams => {
      this.teams = teams;
    });
  }

  onSubmit(): void {
    if (this.selectedPlayer) {
      this.apiService.updatePlayer(this.selectedPlayer.id, this.newPlayer).subscribe(() => {
        this.loadPlayers();
        this.clearForm();
      });
    } else {
      this.apiService.createPlayer(this.newPlayer).subscribe(() => {
        this.loadPlayers();
        this.clearForm();
      });
    }
  }

  editPlayer(player: any): void {
    this.selectedPlayer = player;
    this.newPlayer = { ...player };
  }

  deletePlayer(id: number): void {
    this.apiService.deletePlayer(id).subscribe(() => {
      this.loadPlayers();
    });
  }

  clearForm(): void {
    this.newPlayer = {};
    this.selectedPlayer = null;
  }
}
